<template>
  <div>
    <!-- 头部面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card shadow="always">
      <el-button type="primary" @click="moreRoles">添加角色</el-button>
      <el-table :data="roleList" border>
        <el-table-column type="expand">
          <!-- 一级 二级 三级 列表 -->
          <template slot-scope="scope">
            <el-row
              :class="[index==0?'border-top':'border-bottom','vcenter']"
              v-for="(item,index) in scope.row.children"
              :key="index"
            >
              <el-col :span="5">
                <!-- 一级 -->
                <el-tag>{{item.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row
                  :class="[i===0?'':'border-top','vcenter']"
                  v-for="(items,i) in item.children"
                  :key="i"
                >
                  <el-col :span="6">
                    <!-- 二级 -->
                    <el-tag type="success">{{items.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <!-- 三级 -->
                    <el-tag
                      v-for="(ele,ind) in items.children"
                      :key="ind"
                      type="warning"
                      closable
                      @click="deleteTrid(scope.row,ele.id)"
                    >{{ele.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <!-- <pre>
                      {{scope.row.children}}
            </pre>-->
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="roleName" label="角色名称"></el-table-column>
        <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
              @click="editRoles(scope.row)"
            >编辑</el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete" @click="deleRole(scope.row)">删除</el-button>
            <el-button
              type="warning"
              size="mini"
              icon="el-icon-setting"
              @click="showDia(scope.row)"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
   
   <!-- 添加角色对话框 -->
     <el-dialog title="添加角色" :visible.sync="addMoreRole" width="50%">
      <el-form :model="ruleForm" :rules="rules" ref="ruleFormRef" label-width="80px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="ruleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="ruleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelRole">取 消</el-button>
        <el-button type="primary" @click="addMoreSure">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 编辑角色对话框 -->
    <el-dialog title="修改角色" :visible.sync="addRoles" width="40%">
      <el-form :model="ruleForm" :rules="rules" ref="ruleFormRef" label-width="80px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="ruleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="ruleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoles = false">取 消</el-button>
        <el-button type="primary" @click="editSuccess">确 定</el-button>
      </span>
    </el-dialog>
   

    <!-- 分配权限对话框 -->
    <el-dialog title="分配权限" :visible.sync="roleDia" width="40%" @close="closeDia">
      <!-- 树形节点 -->
      <el-tree
        :data="allRole"
        :props="defaultProps"
        show-checkbox
        node-key="id"
        :default-expand-all="true"
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="roleDia = false">取 消</el-button>
        <el-button type="primary" @click="checkRef">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      roleList: [],
      allRole: [],
      roleDia: false,
      defaultProps: {
        children: 'children',
        label: 'authName',
      },
      defKeys: [],
      roleId: '',
      addRoles: false,
      ruleForm: {
        roleName: '',
        roleDesc: '',
      },
      ruleFormRef: '',
      rules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
        ],
        roleDesc: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
        ],
      },
      addMoreRole:false
    }
  },
  methods: {
    async getRoleList() {
      const { data: res } = await this.$http.get('roles')
      //   console.log(res)
      if (res.meta.status != 200) return this.$message.error('角色列表请求失败')
      this.roleList = res.data
    },
    async deleteTrid(role, rightId) {
      const deleteInfo = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)
      if (deleteInfo != 'confirm') {
        return this.$message.info('取消删除')
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      //   console.log(res)
      if (res.meta.status != 200) return this.$message.error('角色删除失败')
      this.getRoleList()
      //   console.log('确认删除')
    },
    async showDia(role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')

      if (res.meta.status != 200) return this.$message.error('请求角色权限失败')
      this.allRole = res.data
      console.log(this.allRole)
      this.getLeftKeys(role, this.defKeys)
      this.roleDia = true
    },
    getLeftKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach((item) => {
        this.getLeftKeys(item, arr)
      })
    },
    closeDia() {
      this.defKeys = []
    },
    async checkRef() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      )
      if (res.meta.status != 200) return this.$message.error('分配权限失败')
      this.$message.success('权限分配成功')
      this.getRoleList()
      this.roleDia = false
    },
    // 编辑用户
    async editRoles(roleId) {
      //  console.log(id)
      const { data: res } = await this.$http.put(`roles/${roleId.id}`, {
        roleName: roleId.roleName,
        roleDesc: roleId.roleDesc,
      })
      window.console.log(res)
      this.ruleForm = res.data
      this.addRoles = true
    },
    editSuccess() {
      this.$refs.ruleFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) return
        const { data: res } = await this.$http.put(
          'roles/' + this.ruleForm.roleId,
          { roleName: this.ruleForm.roleName, roleDesc: this.ruleForm.roleDesc }
        )
        console.log(res)
        if (res.meta.status != 200) return this.$message.error('编辑用户失败')
        this.$message.success('编辑用户成功')
        this.addRoles = false
        this.getRoleList()
      })
    },
   async deleRole(rId){
    //    window.console.log(rId.id)
          const delResult = await this.$confirm(
        '此操作将永久删除还用户，是否继续？',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)
      
      if (delResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }

      const {data:res} =await this.$http.delete(`roles/${rId.id}`)
      console.log(res)
      if(res.meta.status!=200) return this.$message.error('删除角色失败')
      this.$message.success('删除角色成功')
      this.getRoleList()
    },
    moreRoles(){
      this.addMoreRole=true
    },
    addMoreSure(){
        this.$refs.ruleFormRef.validate(async (valid)=>{
           if(!valid) return 
           const {data:res} =await this.$http.post('roles',this.ruleForm)
           console.log(res)
           if(res.meta.status!=201) return this.$message.error('添加用户失败')
           this.$message.success('添加角色成功')
           this.getRoleList()
           this.addMoreRole=false
        })
    },
    cancelRole(){
        this.ruleForm={}
        this.ruleFormRef=''
         this.addMoreRole=false   

    }

  },
  mounted() {
    this.getRoleList()
  },
}
</script>

<style>
.el-card {
  margin-top: 15px;
}
.el-tag {
  margin: 7px;
}
.border-top {
  border-top: 1px solid #eee;
}
.border-bottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>